import MidSwapper from "./mid/MidSwipper";
import QuickShow from "components/common/quickShow/QuickShow";
import BlogList from "components/common/blogList/BlogList";
import getListDataMixin from "@/mixins/getListDataMixin";
export default {
  name: "MidContent",
  mixins: [getListDataMixin],
  components: {
    MidSwapper: MidSwapper,
    QuickShow: QuickShow,
    BlogList: BlogList
  },
  created: function created() {
    var _this = this;

    //listData位于getListDataMixin中,为返回的博客list(title,desc,createTime,tags)数据
    this.getListData().then(function (res) {
      _this.swiperData = _this.listData.slice(0, 3);
      _this.quickShowData = _this.listData.slice(3, 6);
      _this.blogListData = _this.listData.slice(6);
    });
  },
  data: function data() {
    return {
      swiperData: [],
      quickShowData: [],
      blogListData: []
    };
  },
  methods: {}
};